.list-product {
    @include flex(center,space-between);
    flex-wrap: wrap;
    padding: 1%;
    &-item {
        width: 24%;
        border: 1px solid map-get($color-collection, Gray);
        margin-top: 1%;
        // min-height: 270px;
        .wrapper-contents {
            @include flex(center,flex-start);
            figure {
                min-width: 130px;
            }
        }
        .right {
            padding: 20px 0;
            height: auto;
            .categories {
                color: map-get($color-collection, Gray);
                font-size: 1.2rem;
                min-height: 50px;
            }
        }
        .title {
            display: block;
            min-height: 90px;
            display: flex;
            align-items: center;
        }
        .price {
            margin-right: 20px;
        }
        .sales {
            position: relative;
            color: map-get($color-collection, Red );
        }
        .cancel {
            position: relative;
        }
        .cancel::before {
            content: '-';
            display: inline-block;
            width: auto;
            height: 2px;
            position: absolute;
            left: 0;
            right: 0;
            top: 50%;
            background-color: map-get($color-collection, Black );
            overflow: hidden;
        }
    }
    .rating { 
        @include flex(flex-start,flex-start);
        border: none;
        float: left;
    }
    
    .rating > input { display: none; } 
    .rating > label:before { 
        margin: 0 6px 0 6px;
        font-size: 1.2em;
        font-family: FontAwesome;
        display: inline-block;
        content: "\f005";
        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color:#ffb25d;
    }
    .rating > label { 
        color: #ffffff; 
        float: right; 
    }
    // .rating > input:checked ~ label, /* show gold star when clicked */
    // .rating:not(:checked) > label:hover, /* hover current star */
    // .rating:not(:checked) > label:hover ~ label { 
    //     color: #ffb25d;  
    // }
     /* hover previous stars in list */
    
    // .rating > input:checked + label:hover,
    // .rating > input:checked ~ label:hover,
    // .rating > label:hover ~ input:checked ~ label, 
    // .rating > input:checked ~ label:hover ~ label { 
    //     color: #ffb25d;  
    // }
    .rating > label:hover ~ input:checked ~ label,
    .rating > input:checked + label:hover {
        color: #ffb25d; 
    }
}
