.header {
	display: flex;
	height: $header-height;
	position: fixed;
	width: 100%;
	.wrapper_menu {
			display: flex;
			align-items: flex-start;
			height: 70px;
			width: 100%;
		.logo {
			height: 100%;
			@include flex(center,center)
		}
	}
	.nav {
		margin-left: 20px;
		height: 100%;
		> ul {
			height: 100%;
			display: flex;
			align-items: center;
			margin: 0;
		}
		.item {
			margin-left: 50px;
			padding: 25px 0;
			.list-sub-item {
				position: absolute;
				background-color: #ffffff;
				margin-top: 20px;
				opacity: 0;
				visibility: hidden;
				z-index: -1;
				box-shadow: 2px 8px 15px rgba(68, 68, 68, 0.6);
				:hover {
					opacity: 1;
					visibility: visible;
				}
			}
			&:hover .list-sub-item {
				opacity: 1;
				visibility: visible;
			}
		}
		.sub-item {
			height: 50px;
			width: 180px;
			padding: 0 20px;
			@include flex(center,flex-start);
			border-bottom: 1px solid;
			&:hover > a {
				padding-left: 20px;
				transition-duration: 0.5s;
				transition-timing-function: ease-out;
			}
		}
		}
}
.active {
	background-color: rgba($color: #ffffff, $alpha: 1.0);
}

